import styled from "styled-components";

export const Wrapper = styled.div`
  /* index page style sheet */

  /* .started-img {
    
} */
  .started-img > img {
    width: 100%;
  }
  .will-section {
    margin-top: 6%;
    margin-bottom: 6%;
  }
  .will-div {
    box-shadow: 0px 0px 10px #ececec;
    border-radius: 5px;
    position: relative;
    width: 22%;
    margin: 15px;
  }
  .will-icon {
    position: absolute;
    right: 10%;
    top: 40%;
    background-color: #fff;
    padding: 1% 4% 2% 4%;
    font-size: 30px;
    border: 1px solid #fff;
    border-radius: 100%;
    color: #033168;
    box-shadow: 0 0 10px #d7d7d7;
  }
  /* .top-image {

} */

  .will-div > .top-image > img {
    width: 100%;
    margin-top: 6%;
    border-radius: 14px 14px 0px 0px;
  }

  .will-details {
    padding: 5%;
  }
  .will-details > .detail-heading {
    font-size: 19px;
    color: #003069;
  }
  .will-details > .detail-p {
    font-size: 12px;
    line-height: 20px;
    color: #949494;
  }
  .will-details > .learn-more {
    background-color: #ececec;
    padding: 8px;
  }

  .will-details > .learn-more > a {
    font-size: 12px;
    color: #033168;
  }

  .will-details > .learn-more > i {
    color: #fff;
    float: right;
    background-color: red;
    padding: 10px;
    margin-top: -7px;
    margin-right: -7px;
  }
  .forms-section {
    margin-bottom: 5%;
  }
  .witnesa-page-form {
    text-align: center;
  }
  .witnesa-page-form > span {
    background: #00387b;
    padding: 20px 11px 27px 14px;
    font-size: 24px;
    color: white;
    margin-left: -5px;
    font-weight: 500;
  }
  .self-affidevit-form {
    text-align: center;
  }
  .self-affidevit-form > span {
    background: #d81212;
    padding: 21px 11px 27px 14px;
    font-size: 24px;
    color: white;
    margin-left: -5px;
    font-weight: 500;
    width: 100%;
  }
  .last-will {
    margin-top: 5%;
  }

  .last-will-section {
    /* margin-bottom: 5%; */
    padding-top: 5%;
    position: relative;
  }
  .upper-section {
    background-color: #f7f7f7;
    /* border-radius: 0% 0% 0% 30%; */
  }
  .lower-section {
    background-color: #020035;
  }
  .last-will > .heading-border {
    border-left: 3px solid #033168;
  }
  .last-will > .heading-border > .heading {
    font-size: 21px;
    font-family: auto;
    margin-left: 15px;
    color: #033168;
  }
  .last-will > p {
    font-size: 13px;
    margin: 5%;
  }
  .last-will-img {
    margin-top: 10%;
    margin-bottom: 4.5%;
  }
  .last-will-img > img {
    border: 1px solid #e2e2e2;
    padding: 16px;
    box-shadow: 0 0 10px #d7d7d7;
    border-radius: 22px;
    width: 85%;
  }
  .count {
    background: #71c9ff;
    /* margin: 50px; */
    border-radius: 100%;
    box-shadow: inset 0px 0px 0px 3px white;
    border: 1px solid #71c9ff;
    font-size: 30px;
    color: #fff;
    padding: 9px 18px 12px 18px;
  }
  .mini-heading {
    font-size: 22px;
    color: #033168;
    margin-top: 5%;
  }
  .mini-para {
    font-size: 13px;
    color: #6f6f6f;
  }
  .info-border {
    border-left: 3px solid #fff;
    margin-top: 10%;
    margin-bottom: 5%;
    color: #fff;
    text-align: center;
  }
  .info-heading {
    font-size: 21px;
    font-family: auto;
    margin-left: 15px;
    color: #fff;
  }
  .Nationwide {
    color: #fff;
    text-align: center;
    font-size: 24px;
    line-height: 26px;
  }
  .info-detail {
    color: #fff;
    text-align: center;
    font-size: 20px;
    line-height: 3px;
    font-family: serif;
    margin-bottom: 3.5%;
  }
  .info-mini-detail {
    color: #fff;
    margin-bottom: 5%;
  }
  .info-mini-detail-b {
    color: #fff;
    margin-bottom: 15%;
  }

  .last-will-section > .make-your-will {
    position: absolute;
    top: 95%;
    right: 25%;
  }
  .last-will-section > .make-your-will > img {
    border-radius: 15px;
  }
  .last-will-section > .make-your-will > button {
    right: 10%;
    top: 35%;
    padding: 8px 8px 5px 8px;
    position: absolute;
    border-radius: 25px;
    color: #f34531;
    border: none;
    background-color: #fff;
    font-weight: bold;
    font-size: 17px;
  }
`;
