import styled from "styled-components";

export const Wrapper = styled.section`
  .started-img > img {
    width: 100%;
  }
  .welcome-section {
    margin-top: 5%;
    margin-bottom: 5%;
  }
  .w-heading {
    text-align: center;
    margin-bottom: 2%;
  }
  .w-heading > h1 {
    font-size: 41px;
    color: #033168;
  }
  .w-detail {
    text-align: center;
    margin-bottom: 2%;
    margin-top: 6%;
  }

  .w-detail > span {
    background-color: #ccffcc;
    padding: 12px 60px 12px 56px;
    color: green;
    border-radius: 25px;
  }
  .w-mini-heading {
    text-align: center;
    margin-bottom: 2%;
  }
  .w-mini-heading > h1 {
    color: #033168;
  }
  .w-mini-detail {
    text-align: center;
    margin-bottom: 2%;
  }
  .w-mini-detail > p {
    font-size: 23px;
  }

  .last-section {
    position: relative;
    padding: 5%;
  }
  .last-section > .make-will {
    position: absolute;
    top: 55%;
    right: 25%;
    /* background-color: #f34531; */
  }
  .last-section > .make-will > img {
    border-radius: 15px;
  }
  .make-will > button {
    right: 10%;
    top: 35%;
    padding: 8px 8px 5px 8px;
    position: absolute;
    border-radius: 25px;
    color: #f34531;
    border: none;
    background-color: #fff;
    font-weight: bold;
    font-size: 17px;
  }
  .left-side-text {
    text-align: center;
    font-size: 40px;
    line-height: 44px;
    color: #033168;
  }
  .right-side-text {
    font-size: 30px;
    color: #bbbbbb;
    line-height: 47px;
  }

  .with-icon-section {
    background-color: #f1f1f1;
    padding: 5%;
  }
  .with-icon-div {
    border: 1px solid #fff;
    padding: 16px;
    box-shadow: 0 0 10px #e8e8e8;
    background-color: #fff;
  }
  .left-side-with-icon {
    text-align: center;
    font-size: 32px;
    line-height: 44px;
    color: #033168;
  }
  .right-side-with-icon {
    font-size: 18px;
    color: #bbbbbb;
    line-height: 35px;
  }
  .w-icon-div {
    position: relative;
    text-align: center;
  }
  .w-icon {
    position: absolute;
    right: 44%;
    top: 20%;
    font-size: 37px;
    background-color: white;
    padding: 3px 14px 3px 14px;
    color: #033168;
    border-radius: 72px;
    border: 1px solid #ffffff;
    box-shadow: 0px 0px 10px #ececec;
  }
`;
